/*# sourceMappingURL=bootstrap.min.css.map */
/*------------------------------------------------
	Base Styles
-------------------------------------------------*/
body {
  font-family: "Libre Baskerville", serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: #ababab;
}

a {
  color: inherit;
  transition: all 0.3s ease 0s;
}
a:hover, a:focus {
  color: #ababab;
  text-decoration: none;
  outline: 0 none;
}

h1, h2, h3,
h4, h5, h6 {
  color: #1e2530;
  font-family: "Open Sans", sans-serif;
  margin: 0;
  line-height: 1.3;
}

p {
  margin-bottom: 20px;
}
p:last-child {
  margin-bottom: 0;
}

/*
 * Selection color
 */

::selection {
  background-color: #FA6862;
  color: #fff;
}

/*
 *  Reset bootstrap's default style
 */
.form-control::-webkit-input-placeholder,
::-webkit-input-placeholder {
  opacity: 1;
  color: inherit;
}

.form-control:-moz-placeholder,
:-moz-placeholder {
  /* Firefox 18- */
  opacity: 1;
  color: inherit;
}

.form-control::-moz-placeholder,
::-moz-placeholder {
  /* Firefox 19+ */
  opacity: 1;
  color: inherit;
}

.form-control:-ms-input-placeholder,
:-ms-input-placeholder {
  opacity: 1;
  color: inherit;
}

button, input, select,
textarea, label {
  font-weight: 400;
}

.btn {
  transition: all 0.3s ease 0s;
}
.btn:hover, .btn:focus, .btn:active:focus {
  outline: 0 none;
}

.btn-primary {
  background-color: #FA6862;
  border: 0;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  height: 48px;
  line-height: 50px;
  padding: 0 42px;
  text-transform: uppercase;
}
.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary:active:focus {
  background-color: #f9423a;
}

.btn-border {
  border: 1px solid #d7d8db;
  display: inline-block;
  padding: 7px;
}

/*
 *  CSS Helper Class
 */
.clear:before, .clear:after {
  content: " ";
  display: table;
}
.clear:after {
  clear: both;
}

.pt-table {
  display: table;
  width: 100%;
  height: calc(100vh - 4px);
}

.pt-tablecell {
  display: table-cell;
  vertical-align: middle;
}

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.relative {
  position: relative;
}

.primary,
.link:hover {
  color: #FA6862;
}

.no-gutter {
  margin-left: 0;
  margin-right: 0;
}
.no-gutter > [class^="col-"] {
  padding-left: 0;
  padding-right: 0;
}

.flex {
  display: -moz-flex;
  display: flex;
}

.flex-middle {
  -moz-align-items: center;
  align-items: center;
}

.space-between {
  -moz-justify-content: space-between;
  justify-content: space-between;
}

.nicescroll-cursors {
  background: #FA6862 !important;
}

.preloader {
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1000;
  display: -moz-flex;
  display: flex;
}
.preloader.active.hidden {
  display: none;
}

.loading-mask {
  background-color: #FA6862;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 20%;
  transition: all 0.6s cubic-bezier(0.61, 0, 0.6, 1) 0s;
}
.loading-mask:nth-child(2) {
  left: 20%;
  transition-delay: 0.1s;
}
.loading-mask:nth-child(3) {
  left: 40%;
  transition-delay: 0.2s;
}
.loading-mask:nth-child(4) {
  left: 60%;
  transition-delay: 0.3s;
}
.loading-mask:nth-child(5) {
  left: 80%;
  transition-delay: 0.4s;
}

.preloader.active.done {
  z-index: 0;
}
.preloader.active .loading-mask {
  width: 0;
}

/*------------------------------------------------
	Start Styling
-------------------------------------------------*/
.site-wrapper {
  border-top: 4px solid #FA6862;
}

.page-close {
  font-size: 30px;
  position: absolute;
  right: 30px;
  top: 30px;
  z-index: 100;
}

.page-title {
  margin-bottom: 75px;
}
.page-title img {
  margin-bottom: 20px;
}
.page-title h2 {
  font-size: 68px;
  margin-bottom: 25px;
  position: relative;
  z-index: 0;
  font-weight: 900;
  text-transform: uppercase;
}
.page-title p {
  font-size: 16px;
}
.page-title .title-bg {
  color: rgba(30, 37, 48, 0.07);
  font-size: 158px;
  left: 0;
  letter-spacing: 10px;
  line-height: 0.7;
  position: absolute;
  right: 0;
  top: 50%;
  z-index: -1;
  transform: translateY(-50%);
}

.section-title {
  margin-bottom: 20px;
}
.section-title h3 {
  display: inline-block;
  position: relative;
}
.section-title h3::before, .section-title h3::after {
  content: "";
  height: 2px;
  position: absolute;
  bottom: 8px;
  left: calc(100% + 14px);
}
.section-title h3::before {
  background-color: #1e2530;
  width: 96px;
  bottom: 14px;
}
.section-title h3::after {
  background-color: #FA6862;
  width: 73px;
}
.section-title.light h3 {
  color: #fff;
}
.section-title.light h3::before {
  background-color: #fff;
}

.page-nav {
  bottom: 40px;
  left: 0;
  position: absolute;
  right: 0;
}
.page-nav span {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 0.9;
  text-transform: uppercase;
}

/*------------------------------------------------
    Home Page
-------------------------------------------------*/
.page-home {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  vertical-align: middle;
}
.page-home .overlay {
  background-color: rgba(255, 255, 255, 0.93);
}

/* End of container */
.hexagon-item {
  cursor: pointer;
  width: 200px;
  height: 173.20508px;
  float: left;
  margin-left: -29px;
  z-index: 0;
  position: relative;
  transform: rotate(30deg);
}
.hexagon-item:first-child {
  margin-left: 0;
}
.hexagon-item:hover {
  z-index: 1;
}
.hexagon-item:hover .hex-item:last-child {
  opacity: 1;
  transform: scale(1.3);
}
.hexagon-item:hover .hex-item:first-child {
  opacity: 1;
  transform: scale(1.2);
}
.hexagon-item:hover .hex-item:first-child div:before,
.hexagon-item:hover .hex-item:first-child div:after {
  height: 15px;
}
.hexagon-item:hover .hex-item div::before,
.hexagon-item:hover .hex-item div::after {
  background-color: #FA6862;
}
.hexagon-item:hover .hex-content svg {
  transform: scale(0.97);
}

.page-home .hexagon-item:nth-last-child(1),
.page-home .hexagon-item:nth-last-child(2),
.page-home .hexagon-item:nth-last-child(3) {
  transform: rotate(30deg) translate(87px, -80px);
}

.hex-item {
  position: absolute;
  top: 0;
  left: 50px;
  width: 100px;
  height: 173.20508px;
}
.hex-item:first-child {
  z-index: 0;
  transform: scale(0.9);
  transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.hex-item:last-child {
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
  z-index: 1;
}
.hex-item div {
  box-sizing: border-box;
  position: absolute;
  top: 0;
  width: 100px;
  height: 173.20508px;
  transform-origin: center center;
}
.hex-item div::before, .hex-item div::after {
  background-color: #1e2530;
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1) 0s;
}
.hex-item div:before {
  top: 0;
}
.hex-item div:after {
  bottom: 0;
}
.hex-item div:nth-child(1) {
  transform: rotate(0deg);
}
.hex-item div:nth-child(2) {
  transform: rotate(60deg);
}
.hex-item div:nth-child(3) {
  transform: rotate(120deg);
}

.hex-content {
  color: #fff;
  display: block;
  height: 180px;
  margin: 0 auto;
  position: relative;
  text-align: center;
  transform: rotate(-30deg);
  width: 156px;
}
.hex-content .hex-content-inner {
  left: 50%;
  margin: -3px 0 0 2px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}
.hex-content .icon {
  display: block;
  font-size: 36px;
  line-height: 30px;
  margin-bottom: 11px;
}
.hex-content .title {
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 24px;
  text-transform: uppercase;
}
.hex-content svg {
  left: -7px;
  position: absolute;
  top: -13px;
  transform: scale(0.87);
  z-index: -1;
  transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1) 0s;
}
.hex-content:hover {
  color: #fff;
}

/*------------------------------------------------
    Welcome Page
-------------------------------------------------*/
.author-image-large {
  position: absolute;
  right: 0;
  top: 0;
}
.author-image-large img {
  height: calc(100vh - 4px);
}

/*------------------------------------------------
    About Page
-------------------------------------------------*/
.about-author {
  margin-bottom: 23px;
  -moz-align-items: center;
  align-items: center;
  display: -moz-flex;
  display: flex;
}
.about-author .author-thumb {
  background-color: #fff;
  border: 5px solid #1e2530;
  margin-right: 30px;
  padding: 5px;
}
.about-author .author-thumb img {
  border: 2px solid #1e2530;
  width: 145px;
}
.about-author .author-desc p {
  color: #1e2530;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 1px;
  margin-bottom: 4px;
  text-transform: uppercase;
}
.about-author .author-desc b {
  font-family: "Open Sans", sans-serif;
}

.progress {
  background-color: #1e2530;
  border-radius: 2px;
  height: auto;
  min-height: 45px;
  -moz-align-items: center;
  align-items: center;
  display: -moz-flex;
  display: flex;
}
.progress > div {
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  letter-spacing: 1px;
}
.progress .skill-name {
  border-right: 2px solid #2e333a;
  min-width: 160px;
  text-align: center;
  text-transform: uppercase;
}
.progress .skill-bar {
  background-color: #07080b;
  border-radius: 15px;
  margin: 0 15px;
  -moz-flex-grow: 1;
  flex-grow: 1;
}
.progress .bar {
  background-color: #FA6862;
  border-radius: 10px;
  height: 8px;
  width: 0;
}
.progress .skill-lavel {
  border-left: 2px solid #2e333a;
  min-width: 70px;
  text-align: center;
  -moz-align-items: flex-end;
  align-items: flex-end;
  transition: all 0.3s ease 0s;
}

.filter {
  margin-bottom: 30px;
}
.filter a {
  border-bottom: 2px solid transparent;
}
.filter .active {
  color: #FA6862;
  border-bottom-color: #FA6862;
}

.works-item {
  font-family: "Open Sans", sans-serif;
  margin-bottom: 30px;
  position: relative;
}
.works-item img {
  width: 100%;
}
.works-item .overlay {
  background-color: rgba(30, 37, 48, 0.96);
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
