  .testimonials .item {
    padding-top: 45px;
  }
  .testimonials .thumb {
    margin-top: -45px;
  }

  .history-item .history-text {
    padding-right: 15px;
  }
}
/*============================================================
	Mobile (Portrait) Design for a width of 320px
==============================================================*/
