    border-left: 0;
    margin: 0 15px;
    min-width: auto;
    text-align: left;
  }

  .service-hex {
    float: none;
    margin: 0 auto 40px;
    width: 60%;
  }
  .service-hex:last-child {
    margin-bottom: 0;
  }
  .service-hex:hover svg {
    transform: scale(1.8);
  }
  .service-hex:hover .content {
    width: 120%;
